import './App.css';
import Header from './components/header/Header';
import React from 'react';
import { BrowserRouter} from 'react-router-dom'
import Start from './components/start/Start';
import StartMobile from './components/start/StartMobile';
import Staf from './components/staf/Staf';
import About from './components/about/About';
import AboutMobile from './components/about/AboutMobile';
import Waves from './components/waves/Waves1';
import $ from 'jquery';
import Waves2 from './components/waves/Waves2';
import Why from './components/why/Why';
import { useState , useEffect  } from 'react';
import Ability from './components/ability/Ability';
import Contact from './components/contact/Contact';
import Footer from './components/footer/Footer';
import { RingLoader } from 'react-spinners';
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();
function App(props) {

  const [data, setData] = useState(null)

  useEffect(() => {
    fetch('/api')
    .then(response => response.json())
    .then(response => setData(response.message))
  }, [])

  // const [loading, setLoading] = useState(false);

  // useEffect(() => {
  //   setLoading(true)
  //   setTimeout(() => {
  //     setLoading(false)
  //   } , 1)
  // } , [])

  const [language, setLanguage] = React.useState(false);
  let translate;
  $(function() {  
    $('.btn')
      .on('mouseenter', function(e) {
        var parentOffset = $(this).offset(),
            relX = e.pageX - parentOffset.left,
            relY = e.pageY - parentOffset.top;
        $(this).find('span').css({top:relY, left:relX})
      })
      .on('mouseout', function(e) {
        var parentOffset = $(this).offset(),
            relX = e.pageX - parentOffset.left,
            relY = e.pageY - parentOffset.top;
        $(this).find('span').css({top:relY, left:relX})
      });
    //$('[href=#]').click(function(){return false});
  });


// if(url.indexOf('http://localhost:3000/en') != -1) {
//   setLanguage(true)
// }
// if(url.indexOf('http://localhost:3000/en') != -1) {
//   setLanguage(false)
// }
  language ? translate = props.state.polish.english : translate = props.state.polish.russian
  return (
    <div className="App">
      {/* {loading ? (
        <div className="preloader">
          <div className="preloader-content">
            <RingLoader className='loader' size={200} color={'#F37A24'}/>
          </div>
        </div>
      ) :*/}
      {( 
      <BrowserRouter>
        <header>
          <Header translate={translate.header} language={language} setLanguage={setLanguage} />
        </header>
        <Start translate={translate.start} language={language} setLanguage={setLanguage}/>
        <StartMobile translate={translate.start} language={language} setLanguage={setLanguage}/>
        <Waves/>
        <Staf translate={translate.staf} language={language} setLanguage={setLanguage}/>
        <About translate={translate.about} language={language} setLanguage={setLanguage}/>
        <AboutMobile translate={translate.about} language={language} setLanguage={setLanguage}/>
        <Waves2/>
        <Why translate={translate.why} language={language} setLanguage={setLanguage}/>
        <Ability translate={translate.ability} language={language} setLanguage={setLanguage}/>
        <Contact translate={translate.contact} language={language} setLanguage={setLanguage}/>
        <Footer translate={translate.footer} language={language} setLanguage={setLanguage}/>
      </BrowserRouter>
      )}
    </div>
  );
}

export default App;
