import React, { useRef , useState , useEffect } from 'react';
import './Contact.css';
import AOS from 'aos';
import 'aos/dist/aos.css';
import '../../App.css';
import { useForm } from "react-hook-form";
import emailjs from '@emailjs/browser';
import InlineError from '../validation/InlineError';
import {validateFullName , validateEmail , validateMessage , validatePhone} from './../validation/Validation'

AOS.init();
function Contact({translate , language}) {

    // const [fullName, setFullName] = useState(''); 
    // const [email, setEmail] = useState('');   
    // const [phone, setPhone] = useState(); 
    // const [message, setMessage] = useState(''); 
    let [animation , setAnimation] = useState(false)


    // const [fullNameError, setFullNameError] = useState(); 
    // const [emailError, setEmailError] = useState(); 
    // const [phoneError, setPhoneError] = useState(); 
    // const [messageError, setMessageError] = useState(); 
    // let [valid , setValid] = useState(false)

    // let nameRef = useRef()
    // let emailRef = useRef()
    // let phoneRef = useRef()
    // let messageRef = useRef()

    // console.log(nameRef.required)

    const {
        register,
        handleSubmit,
        formState: { errors },
        reset,
        trigger,
        getValues
    } = useForm();
    

    const form = useRef();
    const resetFormFields = () => {
        return;
    }


    // react-hook-form
      const onSubmit = (data) => {
      };

    // end of react-hook  
    const sendEmail = () => {

        const fullName = getValues("name")
        const email = getValues("email")
        const phone = getValues("phone")
        const message = getValues("message")

        fetch("/api", {
            method: 'POST',
            headers: { 
              'Content-type': 'application/json'
            },
            body: JSON.stringify({
              email,
              message,
              fullName,
              phone
            })
        })

        // setFullName('')
        // setEmail('')
        // setMessage('')
        // setPhone('')
        reset();
        setAnimation(animation = true)
        if (animation === true) {
            animateBtn()
        }
        setTimeout(() => {
            defaultBtn()
            setAnimation(animation = false)
        } , 4000)
    }; 

    const animateBtn = () => {
        if ( language === true) {
            translate.button = 'Dzikuj'
        }
        if ( language === false) {
            translate.button = 'Thanks'
        }
    }
    const defaultBtn = () => {
        if ( language === true) {
            translate.button = 'Wylij'
        }
        if ( language === false) {
            translate.button = 'Submit'
        }
    }
    


    const actionBtn = () => {
        setAnimation(animation = true)
        if (animation === true) {
            animateBtn()
        }
        setAnimation(animation = false)
        setTimeout(() => {
            defaultBtn()
            setAnimation(animation = false)
        } , 4000)
    }

    return (
        
       <section className="contact" id="contact">
           <div className="between">
            <div>
                 <svg className="waves" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink"
                 viewBox="0 24 150 28" preserveAspectRatio="none" shapeRendering="auto">
                 <defs>
                 <path id="gentle-wave" d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z" />
                 </defs>
                 <g className="parallax">
                 <use  xlinkHref="#gentle-wave" x="48" y="0.00000000000001" fill="#284987" />
                 </g>
                 </svg>
            </div>
            <div className="content flex">
            </div>
        </div>
        <div className="contact-main">
            <div className="contact-left">
                <div className="con-phone" data-aos="fade-down" data-aos-duration="1100">
                    <a href="tel:+375447327737"><i className="bi bi-telephone-fill con-icons"></i></a>
                    <h4>{translate.phone}</h4>
                    <a href="tel:+375447327737">{translate.email}</a>
                </div>
                <div className="con-mail" data-aos="fade-down" data-aos-duration="1100">
                    <a href="mailto:studio@welbex.by"><i className="bi bi-envelope con-icons"></i></a>
                    <h4>Email</h4>
                    <a href="mailto:studio@welbex.by">studio@welbex.by</a>
                </div>
            </div>
            <div className="contact-right">
                <h2 data-aos="fade-down" data-aos-duration="1100">{translate.title}</h2>
                <p className="contact-description" data-aos="fade-down" data-aos-duration="1300">{translate.description}</p>
                <form ref={form} onSubmit={handleSubmit(sendEmail)} onReset={resetFormFields}>
                    
                    <input
                        // value={fullName}
                        // onChange={(e) => setFullName(e.value)}
                        // ref={nameRef}
                        // value={fullName}
                        id='input-def'
                        required
                        className={`form-control ${errors.name && "invalid"}`}
                        data-aos="fade-down"
                        data-aos-duration="800"
                        name="name" type="text"
                        placeholder={(translate.placeholder.name)}
                        {...register("name", { required: "Name is Required" })}
                        onKeyUp={() => {
                            trigger("name");
                        }}
                    />

                    {errors.name && (
                        <small className="text-danger">{errors.name.message}</small>
                    )}
                    
                    <input
                        // value={email}
                        // onChange={(e) => setEmail(e.value)}
                        // ref={emailRef}
                        // value={email}
                        id='input-def'
                        required
                        className={`form-control ${errors.email && "invalid"}`}
                        data-aos="fade-down"
                        data-aos-duration="800" 
                        name="email" 
                        type="mail" 
                        placeholder={translate.placeholder.email}
                        {...register("email", { required: "Email is Required" ,
                        pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                            message: "Invalid email address",
                        }})}
                        onKeyUp={() => {
                            trigger("email");
                        }}
                    />

                    {errors.email && (
                        <small className="text-danger">{errors.email.message}</small>
                    )}

                    <input 
                        // value={phone}
                        // onChange={(e) => setPhone(e.value)}
                        // ref={phoneRef}
                        // value={phone}
                        id='input-def'
                        required
                        className={`form-control ${errors.phone && "invalid"}`}
                        data-aos="fade-down" 
                        data-aos-duration="800" 
                        name="phone" 
                        type="text" 
                        placeholder={translate.placeholder.phone}
                        {...register("phone", { required: "Phone is Required",
                        pattern: {
                            value: /^\s*(?:\+?(\d{1,3}))?[-. (]*(\d{3})[-. )]*(\d{3})[-. ]*(\d{4})(?: *x(\d+))?\s*$/,
                            message: "Invalid phone no",
                        },
                        })}
                        onKeyUp={() => {
                            trigger("phone");
                        }}
                    />
                    {errors.phone && (
                        <small className="text-danger">{errors.phone.message}</small>
                    )}

                    <input 
                        // value={message}
                        // ref={messageRef}
                        // onChange={(e) => setMessage(e.value)}
                        className='message'
                        data-aos="fade-down" 
                        data-aos-duration="800" 
                        name="message" 
                        type="text" 
                        placeholder={translate.placeholder.textarea}
                        {...register("message", { required: "Message is Required" })}
                        onKeyUp={() => {
                            trigger("message");
                        }}
                    />

                    <div className="animate-btn">
                        <span></span>
                        <button
                            className='contact-btn'
                            type='submit'
                            onSubmit={actionBtn}
                            id={animation ? 'actived' : 'contact-btn'}
                            >
                            <p id='btnText'>{translate.button}</p>
                                <div className="check-box">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50">
                                        <path fill="transparent" d="M14.1 27.2l7.1 7.2 16.7-16.8" />
                                    </svg>
                                </div>
                        </button>
                    </div>
                </form>
            </div>  
        </div>
        <div className="between2">
            <div>
                 <svg className="waves waves-footer" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink"
                 viewBox="0 24 150 28" preserveAspectRatio="none" shapeRendering="auto">
                 <defs>
                 <path id="gentle-wave" d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z" />
                 </defs>
                 <g className="parallax">
                 <use  xlinkHref="#gentle-wave" x="48" y="0.00000000000001" fill="#244685" />
                 </g>
                 </svg>
            </div>
            <div className="content flex">
            </div>
        </div>
       </section>

    )
}

export default Contact
