import React from "react";
import './Staf.css';
import Waves from "../waves/Waves1";
import './../../App.css';
import { useTranslation } from 'react-i18next'
function Staf({translate}) {

    // const { t } = useTranslation();
    return (
        <div className="staf" id="staf">
            {/* <Waves/> */}
            <div className="members-content all-margin">
                <h2 data-aos="fade-down" data-aos-duration="500">{translate.h2}</h2>
                <div className="members">
                    <div className="member" data-aos="fade-down" data-aos-duration="500">
                        <img className="memberimg1 memberimg" src="img/manager.PNG"/>
                        <div className="mem-wrapper">
                            <h3 className="member-title">{translate.firstMember.h3}</h3>
                            <p className="member-desc">{translate.firstMember.p}</p>
                        </div>
                    </div>
                <div className="member" data-aos="dade-down" data-aos-duration="700">
                    <img className="memberimg2 memberimg" src="img/designer.PNG"/>
                    <div className="mem-wrapper">
                        <h3 className="member-title">{translate.secondMember.h3}</h3>
                        <p className="member-desc">{translate.secondMember.p}</p>
                    </div>
                </div>
                <div className="member" data-aos="fade-down" data-aos-duration="500">
                    <img className="memberimg3 memberimg" src="img/programmer.PNG"/>
                    <div className="mem-wrapper">
                        <h3 className="member-title">{translate.thirdMember.h3}</h3>
                        <p className="member-desc">{translate.thirdMember.p}</p>
                    </div>
                </div>
        </div>
        </div>
        </div>
    )
}

export default Staf