import './Header.css'
import React from 'react';
import {NavLink } from 'react-router-dom';
import {Link } from 'react-scroll';
import './../../App.css';
import jQuery from "jquery";
import $ from "jquery";
import 'bootstrap-icons/font/bootstrap-icons.css';
import { useTranslation } from 'react-i18next';
import emailjs from '@emailjs/browser';
import ChangeLng from './changelng/ChangeLng';
function Header({translate, language, setLanguage}) {



  const { t } = useTranslation()



  


  function hideMenu(){
    document.querySelector(".menu-icon").setAttribute("type","hidden")
  }
  function showMenuMain(){
    document.querySelector(".menu-icon").setAttribute("type","checkbox");
  }
  function showMenu(){
    setTimeout(showMenuMain, 0.00011)
  }

  function changeLanguageEn() {
    setLanguage(true)
  }
  function changeLanguagePl() {
    setLanguage(false)
  }



  return (
      <div className="all-margin header" id='header'>
        <Link className="header-logo" to="start" spy={true} smooth={true} offset={-150} duration={2000}>
        <img src="/img/logo.svg" className="logo-menu" alt='Logo'/>
        </Link>
    
        <ul className="menu">
          <Link className="btn menu-a" to="staf" spy={true} smooth={true} offset={-100} duration={2000}><li>{translate.team}</li><span></span></Link>
          <Link className="btn menu-a" to="why" spy={true} smooth={true} offset={-100} duration={2000}><li>{translate.hs}</li><span></span></Link>
          <Link className="btn menu-a" to="ability" spy={true} smooth={true} offset={-100} duration={2000}><li>{translate.about}</li><span></span></Link>
          <Link className="btn menu-a" to="contact" spy={true} smooth={true} offset={100} duration={2000}><li>{translate.contact}</li><span></span></Link>
          <Link to=''>{language ? <div className="change-language"><p onClick={changeLanguageEn} className='activeLanguage'>EN </p> &nbsp; &nbsp; <p className='interval'>  | </p> &nbsp; &nbsp;<p onClick={changeLanguagePl}> RU </p>&nbsp; &nbsp;</div> : <div className="change-language "><p onClick={changeLanguageEn}>EN </p> &nbsp; &nbsp; <p className='interval'>  | </p> &nbsp; &nbsp;<p className='activeLanguage' onClick={changeLanguagePl}> RU </p>&nbsp; &nbsp;</div>}</Link>
        </ul>
        <div  className="hamburger-menu">
          <Link to="start-mobile" spy={true} smooth={true} offset={-100} duration={2000}><img src="img/logo.svg" className="hamburger-logo" alt='Logo'/></Link>
          <input className="menu-icon" type="checkbox" id="menu-icon" name="menu-icon"/>
          <label onClick={showMenu}  htmlFor="menu-icon" id="label-menu"></label>
          <nav onClick={showMenu} className="nav"  >     
            <ul  className="pt-5" >
              <li> <Link onClick={hideMenu} to="staf" spy={true} smooth={true} offset={-100} duration={2000}>{translate.team}</Link></li> 
              <li> <Link onClick={hideMenu} to="why" spy={true} smooth={true} offset={-100} duration={2000}>{translate.hs}</Link></li> 
              <li> <Link onClick={hideMenu} to="ability" spy={true} smooth={true} offset={-100} duration={2000}>{translate.about}</Link></li> 
              <li> <Link onClick={hideMenu} to="contact" spy={true} smooth={true} offset={-100} duration={2000}>{translate.contact}</Link></li> 
              <Link onClick={hideMenu} to=''>{language ? <div className="change-language"><p onClick={changeLanguageEn} className='activeLanguage'>EN </p> &nbsp; &nbsp; <p className='interval'>  | </p> &nbsp; &nbsp;<p onClick={changeLanguagePl}> RU </p>&nbsp; &nbsp;</div> : <div className="change-language "><p onClick={changeLanguageEn}>EN </p> &nbsp; &nbsp; <p className='interval'>  | </p> &nbsp; &nbsp;<p className='activeLanguage' onClick={changeLanguagePl}> RU </p>&nbsp; &nbsp;</div>}</Link>
            </ul>
          </nav>
        </div>
        <a href='tel:+48885764015'><i className="bi bi-telephone-fill telephone-mobile"></i></a>
        </div>
  );
}

export default Header;
