import React from "react";
import './Start.css';
import { NavLink } from "react-router-dom";
import { Link } from "react-scroll";
import './../../App.css';

function StartMobile({translate}) {
    return (
    <div className="epic-start-mobile all-margin" id="start-mobile">
        <div className="epic-info-mobile">
        <h1 data-aos="fade-down" data-aos-duration="1100">{translate.h1}</h1>
        </div>
        <div className="epic-img-mobile">
            <img src="img/flik.PNG" className="mainEp1-mobile" />
            <img src="img/epic.PNG" className="mainEp2-mobile" />
            <img src="img/epart1.PNG" className="epart-mobile epart1-mobile" data-aos="fade-right" data-aos-duration="1400" />
            <img src="img/epart2.PNG" className="epart-mobile epart2-mobile" data-aos="fade-down" data-aos-duration="1000"/>
            <img src="img/epart3.PNG" className="epart-mobile epart3-mobile" data-aos="fade-down" data-aos-duration="1300"/>
            <img src="img/epart4.PNG" className="epart-mobile epart4-mobile" data-aos="fade-down" data-aos-duration="1600"/>
        </div>
        <div className="epic-describe-mobile">
            <p data-aos="fade-right" data-aos-duration="1100">{translate.p}</p>
            <div data-aos="zoom-in-up" data-aos-duration="1100" className="div-button"><Link className="btn epic-button" to="contact" spy={true} smooth={true} offset={100} duration={2000}>{translate.button}<span></span></Link></div>
        </div>
  </div>
    )
}

export default StartMobile