import React from "react";
import './About.css';
import '../../App.css'
import Waves from "../waves/Waves1";
import { useTranslation } from 'react-i18next'

function AboutMobile({ translate }) {

    const { t } = useTranslation()

    return (
        <div className="about-mobile">
            <div className="about-mobile1">
                <div className="about-main1" data-aos="fade-up" data-aos-duration="1100">
                    <div className="about-text1">
                        <h2>{translate.h2}</h2>
                    </div>
                </div>
            </div>
            <div className="about-mobile2">
                <div className="about-main2" data-aos="fade-up" data-aos-duration="1100">
                    <div className="about-text2">
                        <p>{translate.p}</p>
                    </div>
                </div>
            </div>
            <div className="about-mobile3">
                <div className="about-main3" data-aos="fade-up" data-aos-duration="1100">
                    <div className="about-img" data-aos="fade-up" data-aos-duration="900">
                        <img src="img/like.png" className="fin1"/>
                        <img src="img/like2.png" className="fin2"/>
                    </div>
                </div>
        </div>
        </div>
    )
}

export default AboutMobile