import React from "react";
import './About.css';
import '../../App.css'

function About({translate}) {

    // const { t } = useTranslation()
    
    return (
        <div className="about" id="about">
            <div className="about-main" data-aos="fade-right" data-aos-duration="1100">
                <div className="about-text">
                    <h2>{translate.h2}</h2>
                    <p>{translate.p}</p>
                </div>
                <div className="about-img" data-aos="fade-down" data-aos-duration="1100">
                    <img src="img/like.png" className="fin1"/>
                    <img src="img/like2.png" className="fin2"/>
                </div>
            </div>
        </div>
    )
}

export default About