import React from "react";
import './Start.css';
import { NavLink } from "react-router-dom";
import AOS from 'aos';
import 'aos/dist/aos.css';
import '../../App.css';
import { useTranslation } from 'react-i18next'
import {Link } from 'react-scroll';
AOS.init();
function Start({translate}) {
    // const { t } = useTranslation()
    return (
        <div className="epic-start" id="start">
            <div className="all-margin start2">
                <div className="epic-info">
                    <h1 data-aos="fade-down" data-aos-duration="1100">{translate.h1}</h1>
                    <p data-aos="fade-right" data-aos-duration="1100">{translate.p}</p>
                    <div data-aos="zoom-in-up" data-aos-duration="1100" className="div-button"><Link className="btn epic-button" to="contact" spy={true} smooth={true} offset={100} duration={2000}>{translate.button}<span></span></Link></div>
                    
                </div>
                <div className="epic-img">
                    <img src="/img/epic.PNG" className="mainEp1" />
                    <img src="/img/epic.PNG" className="mainEp2" />
                    <img src="/img/epart1.PNG" className="epart epart1" data-aos="fade-right" data-aos-duration="1400" />
                    <img src="/img/epart2.PNG" className="epart epart2" data-aos="fade-down" data-aos-duration="1000"/>
                    <img src="/img/epart3.PNG" className="epart epart3" data-aos="fade-down" data-aos-duration="1300"/>
                    <img src="/img/epart4.PNG" className="epart epart4" data-aos="fade-down" data-aos-duration="1600"/>
                </div>
            </div>
        </div>
    )
}

export default Start